<template>
  <div id="bet-button" @click="bet()" class="disabled-bet">
    <input type="number" v-model='amount' v-on:click.stop id="inside">
    <span>ether&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
    <font-awesome-icon icon="play"></font-awesome-icon>
  </div>
</template>

<script>
export default {
    name: "BetButton",
    data() {
      return {
        amount: 0
      }
    },
    props: {
      bool: Boolean
    },
    methods: {
      bet: function () {
        if (this.$appStatus('beforeBet')) return;
        if (this.$STORE.getMyAddress() == 0x0) {
          this.$addMsg("Login with Metamask!")
          return
        }
        if (this.amount == 0) {
          this.$addMsg("Set the amount first!")
          return
        }
        if (this.$STORE.account.balance < this.amount) {
          this.$addMsg("You do not have enough eth to continue!")
          return
        }
        this.$match.bet(this.bool, this.amount)
        .then(res => this.$addMsg("Your transaction is mined."))
        .catch(err => this.$addMsg("Your bet was not placed. No payment made."))
        .then(() => this.amount = 0)
      }
    }
}
</script>

<style scoped>
#inside {
  background-color: gold;
  color: black;
  border: 1px solid gold;
  text-align: right;
  width: 75px;
}

#inside:hover {
  background-color: white !important;
  color: black !important;
}

#inside:active {
  outline: 0;
  background-color: white !important;
}

span {
  font-style: italic;
}

#inside:focus {
  outline: 0;
  background-color: white !important;
}

#bet-button {
  padding: 10px;
  margin: 10px;
  border: 1px solid gold;
  border-radius: 3px;
  background-color: gold;
  color: black;
  cursor: pointer;
  box-shadow: 0 0 5px #555;
}

#bet-button:hover {
  box-shadow: 0 0 1px #555;
}

#bet-button:hover input {
  background-color: gold;
  border: 1px solid black;
  border-radius: 3px;
  color: black;
}
</style>



