<template>
  <div class="container-fluid" id="account">
    <div class="fl" v-if="20 <= totalPot">
      Total pot: {{ totalPot }} eth
    </div>
    <div class="fr">
      <div class="account-text">
        <p>Logged in as:&nbsp;{{ $STORE.getMyAddress() }}</p>
        <p>My balance:&nbsp;{{ $STORE.getMyBalance() }}&nbsp;eth</p>
      </div>
      <div class="account-refresh">
        <button class="btn btn-outline-secondary" @click="$ACTION.$emit('Reload Data')">
          <p id="smaller">
            <font-awesome-icon icon="sync-alt"></font-awesome-icon>
          </p>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name: "Account",
    data() {
      return {

      }
    },
    methods: {

    },
    computed: {
      totalPot: function () {
        return parseFloat(web3.fromWei(this.$STORE.getTotalPotForHomeTeam(), "ether")) + parseFloat(web3.fromWei(this.$STORE.getTotalPotForAwayTeam(), "ether"))
      }
    }
}
</script>

<style scoped>
#account {
  color: black;
  font-weight: bold;
}

.fr {
  padding-top: 5px;
  padding-right: 5px;
  float: right;
  text-align: right;
  display: flex;
}

.fl {
  font-size: 0.7rem;
  padding-top: 5px;
  margin-left: 20px;
  float: left;
  text-align: right;
}

.account-refresh {
  margin-left: 10px;
}

#smaller {
  font-size: 1.7rem !important !important !important !important !important !important;
}

.fr p {
  margin: 0;
  padding: 0;
  font-size: 0.7rem;
}
</style>



