<template>
  <div id="root">
    <MainContainer>
      <Account></Account>
      <div class="container" id="middle">
        <div class="row">
          <div class="col-md-4">
            <div class="row justify-content-center">
              <div class="col-md-9">
                <div class="my-card">
                  <p class="odds" @click="$openApp('Math')">~ 1.87x</p>
                  <Flag :flags="['portugal']" :flow="'up'"></Flag>
                  <!-- <BetButton :bool="true" v-if="$appStatus('betPeriod')"></BetButton> -->
                  <!-- <Withdraw v-if="$appStatus('withdrawalPeriod')"></Withdraw> -->
                </div>
              </div>
            </div>
          </div>

          <div class="col-md-4">
            <div class="row justify-content-center">
              <div class="col-md-9">
                <img src="../../../../../static/favicon.png" id="worldcupimage">
              </div>
            </div>
          </div>

          <div class="col-md-4">
            <div class="row justify-content-center">
              <div class="col-md-9">
                <div class="my-card">
                  <p class="odds" @click="$openApp('Math')">~ 2.07x</p>
                  <Flag :flags="['brasil']" :flow="'down'"></Flag>
                  <!-- <BetButton :bool="false" v-if="$appStatus('betPeriod')"></BetButton> -->
                  <Withdraw v-if="$appStatus('withdrawalPeriod')"></Withdraw>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Countdown v-if="$appStatus('beforeBet')"></Countdown>
    </MainContainer>

    <ShortText
      @readMore="slide()"
      :firstLine="'Use your ether to bet on the world cup finel here, with the best possible odds, with any amount you wish to, with just one click.'"
      :secondLine="'This bet lives in an incorruptable smart contract on the ethereum blockchain transparently and anonymously.'"
      :thirdLine="'Play now, or collect some more info before that.'">
    </ShortText>

    <Info></Info>

    <Footer></Footer>
  </div>
</template>

<script>
import MainContainer from '../../../common/MainContainer'
import ShortText from '../../../common/ShortText'
import Footer from '../../../common/Footer'
import Flag from './Flag'
import Info from './Info'
import BetButton from './BetButton'
import Account from './Account'
import Countdown from './Countdown'
import Withdraw from './Withdraw'

export default {
  name: 'LandingPage',
  components: {
    MainContainer,
    ShortText,
    Info,
    Flag,
    BetButton,
    Account,
    Countdown,
    Footer,
    Withdraw
  },
  methods: {
    slide: function () {
      window.scrollTo({top: 650, behavior: "smooth"})
    }
  }
}
</script>

<style scoped>
#middle {
  margin-top: 120px;
}

#worldcupimage {
  max-width: 200px;
}

.odds {
  text-align: center;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  color: #555;
}

.my-card {
  padding: 20px;
  background-color: white;
  border-radius: 3px;
  box-shadow: 0 0 5px #555;
}

.hoverable {
  opacity: 0.7;
}

.hoverable:hover {
  opacity: 1;
}
</style>



